import React, { Component } from 'react'
import { Mutation } from 'react-apollo'
import Form from './styles/Form'
import gql from 'graphql-tag'
import styled from 'styled-components'
import Error from './ErrorMessage'
import SickButton from './styles/SickButton'
import { UPDATE_PERMISSIONS_MUTATION } from './Permissions'
import { CURRENT_USER_QUERY } from './User'

const Inner = styled.div`
  max-width: ${props => props.theme.innerWidth};
  margin: 0 auto;
  padding: 2rem;
  .dates {
    font-family: montserrat, sans-serif;
    text-transform: uppercase;
    opacity: 0.5;
    &:focus {
      opacity: 1;
    }
    &:active {
      opacity: 1;
    }
  }
`

class UpdateUserProfile extends Component {
  state = {
    name: '',
    businessName: '',
    email: '',
  }
  handleChange = e => {
    const { name, type, value } = e.target
    const val = type === 'number' ? parseFloat(value) : value
    this.setState({ [name]: val })
  }
  updateUser = async (e, updateUserMutation) => {
    e.preventDefault()
    const res = await updateUserMutation({
      variables: {
        ...this.state,
      },
    })
  }

  render() {
    return (
      <Inner>
        <Mutation
          mutation={UPDATE_PERMISSIONS_MUTATION}
          variables={{ userId: this.props.user.id }}
        >
          {(updateUser, { loading }) => (
            <Form onSubmit={e => this.updateUser(e, updateUser)}>
              <fieldset disabled={loading} aria-busy={loading}>
                <label htmlFor="firstName">
                  Account Manager's Name
                  <input
                    type="text"
                    id="name"
                    name="name"
                    placeholder="Name"
                    required
                    defaultValue={this.state.name}
                    onChange={this.handleChange}
                  />
                </label>
                <label htmlFor="businessName">
                  Edit Display Name:
                  <input
                    type="text"
                    id="businessName"
                    name="businessName"
                    placeholder="Business Display Name"
                    required
                    defaultValue={this.state.businessName}
                    onChange={this.handleChange}
                  />
                </label>
                <label htmlFor="email">
                  Login/Contact Email Address
                  <input
                    type="text"
                    id="email"
                    name="email"
                    placeholder="Email Address"
                    required
                    defaultValue={this.state.email}
                    onChange={this.handleChange}
                  />
                </label>

                <SickButton type="submit">
                  Sav{loading ? 'ing' : 'e'} Changes
                </SickButton>
              </fieldset>
            </Form>
          )}
        </Mutation>
      </Inner>
    )
  }
}

export default UpdateUserProfile
export { UPDATE_USER_MUTATION }
